.card {
  display: flex;
  width: 25%;
  height: 19vh;
  margin: 10px 70px;
  border: 3px solid #000;
  border-radius: 20px;
  box-shadow: 1px 1px 1px 1px #cacaf8;
  overflow: hidden;

  align-items: center;
  text-align: center;
}

.card h3 {
  width: 40%;
}
img {
  width: 60%;
  height: 100%;
}
